




















	export default {
		data() {
			return {
				checkboxItems: [{
                    name: 'USA',
                    value: '美国'
                },
                {
                    name: 'CHN',
                    value: '中国',
                    checked: 'true'
                }
            ],
			};
		},
		methods:{
			checkboxChange(e){
				console.log(e)
				var checked = e.target.value
				var changed = {}
				
				for(let i = 0;i<this.checkboxItems.length;i++){
					if(checked.indexOf(this.checkboxItems[i].name)!==-1){
						changed['checkboxItems['+i+'].checked'] = false
					}else{
						changed['checkboxItems['+i+'].checked'] = true
					}
				}
				
			}
		}
	}
